import React from 'react'
import { Container, Paper, Box, Typography, AppBar, IconButton, makeStyles, Toolbar, Button, useMediaQuery, useTheme} from '@material-ui/core'
import Menu from '@material-ui/core/Menu'
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import './Navbar.css'
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import {Link, useHistory, BrowserRouter} from 'react-router-dom'
import LoginPopup from "./FormDialog";
import LoginPage from './LoginPage';
import data2 from '../App'
import axios from 'axios';




const useStyles = makeStyles({
    header: {
backgroundColor: "#1e212d",
fontFamily: "Libre Baskerville",
letterSpacing: "100px",
padding: "10px 5px 10px 0"
    },
    button: {
        backgroundColor: "#1e212d",
        color: "#f9f3f3",
        textTransform: "initial",
        fontSize: "20px",
        marginleft: "5vh",
        marginRight: "5vh",


        "&:hover": {
           textDecoration: "underline"
        }
    }
})



function Navbar({isAuth}) {
   
    let history = useHistory()

    const logout = () => {
        axios.get("https://surveyeu.herokuapp.com/logout", {
          withCredentials: true
        }).then((res : AxiosResponse) => {
          if (res.data === "success") {
            window.location.href = "/";
          }
        })
      }



    const classes = useStyles();
    const [openPopup, setOpenPopup] = React.useState(false);

    return (
        
        <div>
            <AppBar position='sticky' className= {classes.header}>
               <Toolbar>
                 <IconButton>
                     <Menu />
                 </IconButton>
                 
                 <Link to="https://puneetkathar1.github.io/Surveyu/" style={{textDecoration:"none"}}> <Typography variant = 'h2' style={{color:"#eabf9f"}}> 𝓢 </Typography></Link>
                 <Link to="https://puneetkathar1.github.io/Surveyu/" style={{textDecoration:"none"}}> <Typography variant = 'h4' style={{color: "#f9f3f3", flexGrow: 1, paddingRight: "1150px"}}> urveyeu </Typography> </Link>
                
<LoginPopup
openPopup = {openPopup}
setOpenPopup = {setOpenPopup}
>
</LoginPopup>

                 {isAuth===true ? <Button style={{textDecoration:"none"}} onClick={() =>history.push('/myaccount')}> <AccountCircleIcon fontSize='large' style={{color:'#f9f3f3'}} /> </Button> : null}
                 {isAuth===false ?  <Button style={{textDecoration:"none"}} onClick={() =>setOpenPopup(true)}> <AccountCircleIcon fontSize='large' style={{color:'#f9f3f3'}} /> </Button> : null}
                 {isAuth===true ? <Button style={{textDecoration:"none"}} onClick={logout}> <ExitToAppIcon fontSize='large' style={{color:'#f9f3f3'}} /> </Button> : null}

               </Toolbar>
            </AppBar>
        </div>
    )
}

export default Navbar
