import { Route, Redirect } from 'react-router-dom';



  function  ProtectedRoutes({component: Component, isAuth, ...rest}) {



    return (
<Route
    {...rest}
     render={ (props)  =>  {

        
        if(isAuth){
            console.log(isAuth)
            return <Component />;

        } if(!isAuth) {
            console.log(isAuth)
            return (
                <Redirect to={{ pathname: "https://puneetkathar1.github.io/Surveyu", state: {from: props.location}}} />
            )

        }

    }}
    />
    )
}

export default ProtectedRoutes
